<template>
  <div class="login-container">
    <div class="login-container__asside"></div>
    <div class="login-container__body">
      <div class="title-container">
        <h2>Авторизация</h2>
        <v-progress-circular
            color="green"
            indeterminate
            v-if="isShowAuthLoader"
        ></v-progress-circular>

      </div>
      <div class="input-data">
        <v-text-field class="input-data__login"
                      v-model="login"
                      :rules="loginRules"
                      hide-details="auto"
                      label="Ваш логин"
        ></v-text-field>
        <div class="show-hide-password">
          <v-text-field class="input-data__password"
                        v-model="password"
                        :rules="passwordRules"
                        hide-details="auto"
                        label="Ваш пароль"
                        :type="showPassword ? 'text' : 'password'"
          ></v-text-field>
          <v-icon v-if="showPassword" class="input-data__password-icon" @click="togglePasswordVisibility">mdi-eye
          </v-icon>
          <v-icon v-if="!showPassword" class="input-data__password-icon" @click="togglePasswordVisibility">mdi-eye-off
          </v-icon>
        </div>
      </div>
      <v-btn
          :disabled="!availableConfirmBtn"
          block
          class="confirm-btn"
          @click="checkAuth">
        Войти
      </v-btn>
    </div>
  </div>
  <div class="text-center">
    <v-snackbar
        v-model="snackbar"
        multi-line
        timeout="3000"
        style="margin-bottom: 60px"
    >
      {{ snackbarText }}

      <template v-slot:actions>
        <v-btn
            color="red"
            variant="text"
            @click="snackbar = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
  <div class="text-center">
    <v-dialog
        v-model="dialog"
        max-width="400"
        persistent
    >

      <v-card
          prepend-icon="mdi-information"
          :text="dialogText"
          :title="dialogTitle"
      >
        <template v-slot:actions>
          <v-spacer></v-spacer>

          <v-btn @click="dialog = false" color="red">
            Закрыть
          </v-btn>
        </template>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "LoginComponent",
  components: {},
  data() {
    return {
      dialog: false,
      login: '',
      password: '',
      checkRegEx: {log: false, pass: false},
      availableConfirmBtn: false,
      loginRules: [
        value => !!value || 'Требуется логин.',
        value => (value && value.length >= 3) || 'Логин должен содержать минимум 3 символа',
        value => this.validateLogin(value) || 'Логин должен быть в формате "*.*******"'
      ],
      passwordRules: [
        value => !!value || 'Требуется пароль.',
        value => (value && value.length >= 3) || 'Пароль должен содержать минимум 3 символа',
        value => this.validateEmail(value) || 'Подсказка: пароль должен содержать @'
      ],
      dialogText: '',
      dialogTitle: '',
      isShowAuthLoader: false,
      snackbar: false,
      snackbarText: '',
      showPassword: false,
    }
  },
  methods: {
    togglePasswordVisibility() {
      this.showPassword = !this.showPassword;
    },
    handleEnter(event) {
      if (event.key === 'Enter' && this.availableConfirmBtn) {
        this.checkAuth();
      }
    },
    validateLogin(value) {
      const regex = /^[a-zA-Z]\.[a-zA-Z]+$/;
      this.checkRegEx.log = regex.test(value);
      return regex.test(value);
    },
    validateEmail(value) {
      const regex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      this.checkRegEx.pass = regex.test(value);
      return regex.test(value);
    },
    async checkAuth() {
      this.availableConfirmBtn = false;
      this.isShowAuthLoader = true;
      let isLoginActual = null;

      await $.ajax({
        url: 'https://script.google.com/macros/s/AKfycbzlA3mS-tvOR0onR5y3seF1E8p7CwsUPuhxhKFGVKOf-btPNTqLGOv3V-vtg9c4LKJT5A/exec',
        type: "GET",
        crossDomain: true,
        dataType: "json",
        success: (response) => {
          isLoginActual = response.stuff.filter(item => item.LoginName == this.login);
          if (isLoginActual.length < 1) {
            this.availableConfirmBtn = true;
            this.isShowAuthLoader = false;
            this.dialogTitle = 'Сотрудник не найден';
            this.dialogText = 'Убедитесь, что вводите логин правильно';
            this.dialog = true;
          }
          if (isLoginActual.length > 0) {
            if (isLoginActual[0].PasswordEmail == this.password) {
              let authObj = {login: isLoginActual[0].LoginName, role: isLoginActual[0].RoleStatus};

              if (isLoginActual[0].RoleStatus == 'super' || isLoginActual[0].RoleStatus == 'manager') {
                localStorage.setItem('sale-auth', JSON.stringify(authObj));
                this.$emit('updateUserStatus', 'check');
                this.$router.push('/');
              }
              if (isLoginActual[0].RoleStatus == 'promoter') {
                localStorage.setItem('sale-auth', JSON.stringify(authObj));
                this.$emit('updateUserStatus', 'check');
                this.$router.push('/');
              }
            } else if (isLoginActual[0].PasswordEmail != this.password) {
              this.dialogTitle = 'Неправильный пароль';
              this.dialogText = 'Убедитесь, что вводите пароль правильно';
              this.dialog = true;
              this.availableConfirmBtn = true;
              this.isShowAuthLoader = false;
            }
          }
        },
        error: (error) => {
          console.log(error);
          this.snackbarText = 'Ошибка загрузки данных с вервера, повторите попытку позже'
          this.snackbar = true;
          this.availableConfirmBtn = true;
          this.isShowAuthLoader = false;
        }
      });
    },
  },
  watch: {
    checkRegEx: {
      handler(newVal) {
        if (newVal.log && newVal.pass) {
          this.availableConfirmBtn = true;
        } else {
          this.availableConfirmBtn = false;
        }
      },
      deep: true
    }
  },
  mounted() {
    document.addEventListener('keydown', this.handleEnter);
  },
  beforeUnmount() {
    document.removeEventListener('keydown', this.handleEnter);
    this.login = '';
    this.password = '';
  }
}
</script>

<style lang="scss" scoped>
.login-container {
  max-width: 700px;
  margin: 0 auto;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  position: relative;
  padding: 20px 45px 20px 20px;
  overflow: hidden;
  border-radius: 5px;
  background-color: whitesmoke;

  .login-container__asside {
    position: absolute;
    background-color: rgb(66, 133, 244);
    width: 25px;
    right: 0;
    top: 0;
    bottom: 0;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
  }

  .login-container__body {
    padding: 5px;

    .title-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 50px;

      h2 {
        text-align: left;
        color: rgb(66, 133, 244);
        letter-spacing: 1px;
      }
    }

    .input-data {

      .v-text-field {
        background-color: white;
      }

      .input-data__login {
        margin-bottom: 20px;
      }

      .show-hide-password {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        .input-data__password {

        }

        .input-data__password-icon {
          margin: 10px;
          height: 100%;
          background-color: whitesmoke;
        }
      }

    }

    .confirm-btn {
      margin-top: 50px;
      background-color: rgb(66, 133, 244);
      color: white;
    }
  }
}
</style>